<template>
	<div class="menu">
		<div class="card">
			<h1>Plekjes</h1>
			<p>V0.5.0</p>
		</div>
        <div  v-if="this.$store.state.creatingPlekollection">
            <div class="card">
                <p>new Plekollection</p>
                <span>name: <input v-model="new_plekollection_name"/></span>
                <br><br>
                <button class="btn" @click="savePlekollection()">save</button>
                <br><br>
            </div>
        </div>
		<div v-if="this.$store.state.userData">
			<div class="card">
				<h2>Plekollections</h2>
				<div
					v-for="(p, i) in this.$store.state.userData.my_plekollections"
					class="list"
				>
					<div
						@click="selectPlekollection(p, i)"
						v-bind:class="{ selected: i == selected_i }"
					>
						{{ p.name }}
					</div>
				</div>
				<br />
			</div>
			<div class="card" v-if="selected_plekollection.name">
				<h2>{{ selected_plekollection.name }}</h2>
				<button
					v-if="selected_i != null"
					class="btn"
					@click="showSelectedPlekollectionOnMap()"
				>
					Show on map
				</button>
				<br /><br />
				<div
					v-for="(p, i) in selected_plekollection.plekjes"
					class="list"
				>
					{{ p.name }}
				</div>
				<br />
			</div>
		</div>
        
	</div>
</template>

<script>
var api = require("@/service");
export default {
	data() {
		return {
			selected_i: null,
			selected_plekollection: {},
            new_plekollection_name:null
		};
	},
	methods: {
		selectPlekollection(p, i) {
			this.selected_i = i;
		    this.getPlekollection(this.$store.state.userData.my_plekollections[this.selected_i]._id)
		},
        getPlekollection(plekollection_id) {
			api.apiCall(
				"GET",
				process.env.VUE_APP_API_URL +
					"/plekollections/get_one/" +
					plekollection_id,
				null,
				(e) => {
                    if(e){
                        let mongodata = JSON.parse(e);
					    this.selected_plekollection = mongodata;
                    }
				}
			);
		},
        createFeatureCollection(plekjes) {
			let geojsonFeatureCollection = {
				type: "FeatureCollection",
				name: this.selected_plekollection.name,
				features: [],
			};
			plekjes.forEach((e) => {
				geojsonFeatureCollection.features.push({
					type: "Feature",
					properties: {
						name: e.name,
					},
					geometry: e.geometry,
				});
			});
            this.$store.state.featureCollections.push(geojsonFeatureCollection);
            this.emitter.emit('updateMap');
		},
		showSelectedPlekollectionOnMap() {
            let list_of_ids = this.selected_plekollection.plekjes.map(x => x._id);
			api.getListOfPlekjesFromListOfIds(list_of_ids, (res) => {
				this.createFeatureCollection(res);
			});
        },
        savePlekollection(){
            if(!this.new_plekollection_name|this.new_plekollection_name==' '){
                alert('please enter a plekollection name')
            }else{
                this.emitter.emit('savePlekollection',this.new_plekollection_name);
            }
           
        }
	},
    mounted(){
        if(!this.$store.state.userData){
            this.$store.dispatch("setUserDataAPI");
        }
    }
};
</script>

<style>
.menu {
	width: 25%;
}
.card {
	width: 90%;
	text-align: left;
	margin: 5px auto;
	padding: 0px 5px;
	border: solid;
	border-width: 1.5px;
	border-radius: 5px;
	color: #333;
}

.list div:hover {
	background-color: #ddd;
	color: #333;
}
.list div.selected {
	background-color: #ddd;
	color: #333;
}
.btn {
	background-color: #17a2b8;
	color: #f2f2f2;
	padding: 10px 15px;
	font-size: 16px;
	border: none;
	border-radius: 5px;
}
.btn:hover {
	color: #333;
}
</style>