import Store from "./store"

export function apiCall (type, path, body, callback){
	//console.log("calling API");
	var xobj = new XMLHttpRequest();
	xobj.open(type, path, true);
	xobj.setRequestHeader("Content-Type", "application/json");
	xobj.setRequestHeader(
		"Authorization",
		`Bearer ${localStorage.getItem("access_token")}`
	);
	xobj.send(body);
	xobj.onreadystatechange = () => {
		if (xobj.readyState == 4 && xobj.status == "200") {
			//console.log("API called");
			callback(xobj.responseText);
		}
	};
};

export function getListOfPlekjesFromListOfIds (list_of_ids,callback){
    const query = JSON.stringify({ids: list_of_ids});
	this.apiCall(
		"POST",
		process.env.VUE_APP_API_URL + "/plekjes/get_multiple/",
        query,
		(e) => {
			let mongodata = JSON.parse(e);
            callback(mongodata);
		}
	);
};

export function launchCreatePlekollectionFlow(featcol,name){
    saveFeatColAsPlekjes(featcol,name);
}
const saveFeatColAsPlekjes = (featcol,name) =>{
    let new_plekjes = [];
    for (let i = 0; i < featcol.features.length; i++) {
        new_plekjes.push({
            name: featcol.features[i].properties.name,
            geometry: featcol.features[i].geometry,
            tags: [name],
            founder: Store.default._state.data.userData.username,
        });
    }
    this.apiCall(
        "POST",
        process.env.VUE_APP_API_URL +"/plekjes/add_multiple",
        JSON.stringify(new_plekjes),
        (e) => {
            savePlekjesAsPlekollection(JSON.parse(e),name);
        }
    );
}

const savePlekjesAsPlekollection = (plekjes,name) =>{
    let newPlekcollection = {
        name: name,
        plekjes: plekjes,
        collector: Store.default._state.data.userData.username,
    };
    this.apiCall(
        "POST",
        process.env.VUE_APP_API_URL +"/plekollections/new",
        JSON.stringify(newPlekcollection),
        (e) => {
            addPlekjesAndPlekollectionToUser(JSON.parse(e),plekjes);
        }
    );
}
const addPlekjesAndPlekollectionToUser = (plekollection,plekjes) => {
    const query = JSON.stringify({
        email: Store.default._state.data.userEmail,
    });
    this.apiCall(
        "POST",
        process.env.VUE_APP_API_URL + "/users/get_one",
        query,
        (e) => {
            let user = JSON.parse(e);

            // add plekollections
            user.my_plekollections.push({
                name: plekollection.name,
                _id: plekollection._id,
            });

            user.my_plekjes = user.my_plekjes.concat(plekjes)

            // update user
            this.apiCall(
                "PUT",
                process.env.VUE_APP_API_URL +
                    "/users/update/"+user._id,
                JSON.stringify(user),
                (e) => {console.log('finished upload');Store.dispatch("setUserDataAPI")}
            );
        }
    );
}